import { injectGlobal } from 'styled-components'

// tslint:disable-next-line
injectGlobal`
  html {
    // height: 100%;
    height: auto;
  }

  body {
    background-color: rgba(119, 199, 199, 0.4);
    height: 100%;
    color: #033649;
  }

  #root {
    height: 100%;
  }
`



