import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import * as Bows from 'bows'
import { StoreState } from 'src/store/interface'
import { GameAction } from 'src/actions'
import { ProgressState, GameState } from 'src/reducers'
import { sections, SectionItem } from 'src/data'

const COMPONENT_NAME = 'Tabs'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
`

const TabWrapper = styled.div`
  flex-grow: 1;
  // border: solid 1px red;
  box-sizing: border-box;
  padding: 24px;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  background-color: #93c6d4;
  transition: background-color 0.3s;

  &:hover {
    background-color: #a1cdda;
  }
`

interface Props {
  progress: ProgressState
  game: GameState
  setActiveSection: (section: string) => Promise<void>

  _placeholder: any
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseTabs extends React.Component<Props, State> {
  static defaultProps = {
    _placeholder: '',
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')

    this.setDefaultActiveSection()
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  tabClickHandler(sectionKey: string) {
    log('tabClickHandler triggered. sectionKey:', sectionKey)
    this.props.setActiveSection(sectionKey)
  }

  render(): JSX.Element {
    return <ComponentWrapper>{sections.map((sectionItem) => this.renderTab(sectionItem))}</ComponentWrapper>
  }

  private renderTab(sectionItem: SectionItem): JSX.Element {
    return (
      <TabWrapper key={sectionItem.key} onClick={() => this.tabClickHandler(sectionItem.key)}>
        {sectionItem.label}
      </TabWrapper>
    )
  }

  private setDefaultActiveSection() {
    this.props.setActiveSection('dungeon')
  }
}

function mapStateToProps(state: StoreState) {
  const { progress, game } = state
  return {
    progress,
    game,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setActiveSection: async (section: string): Promise<void> => {
      dispatch(GameAction.setActiveSection(section))
    },
  }
}

const Tabs = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseTabs)
export default Tabs



