export class CastHelper {
  static toString(val: string | undefined, defaultValue = ''): string {
    return val || defaultValue
  }

  static toBoolean(val: string | undefined, defaultValue = false): boolean {
    if (val === 'true') {
      return true
    } else if (val === 'false') {
      return false
    } else {
      return defaultValue
    }
  }
}



