import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import * as Bows from 'bows'
import { StoreState } from 'src/store/interface'
import { TraceState } from 'src/reducers/trace-reducer'
import { LogItem } from 'src/common/interfaces'
import { DateHelper } from 'src/helpers'

const COMPONENT_NAME = 'SectionLog'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  padding: 24px;
  background-color: rgba(119, 199, 199, 0.2);
`

const ComponentInnerWrapper = styled.div`
  // background-color: rgba(255, 0, 255, 0.03);
  height: 160px;
  overflow: auto;
`

const LogContainer = styled.div`
  // background-color: rgba(0, 0, 0, 0.02);
`

const LogWrapper = styled.div`
  display: flex;
  // align-items: center;
  padding: 4px 0;
  font-size: 12px;
  transition: background-color 0.3s;

  &:hover {
    background-color: rgba(0, 0, 0, 0.02);
  }
`

const LogTimestamp = styled.div`
  color: #cdb380;
`

const LogMessage = styled.div`
  margin-left: 6px;
`

interface Props {
  trace: TraceState

  _placeholder: any
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseSectionLog extends React.Component<Props, State> {
  static defaultProps = {
    _placeholder: '',
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  render(): JSX.Element {
    if (this.props.trace.logs.length === 0) {
      return (
        <ComponentWrapper>
          <ComponentInnerWrapper>
            <div>No logs...</div>
          </ComponentInnerWrapper>
        </ComponentWrapper>
      )
    }

    return (
      <ComponentWrapper>
        <ComponentInnerWrapper>
          <LogContainer>{this.props.trace.logs.map((item) => this.renderItem(item))}</LogContainer>
        </ComponentInnerWrapper>
      </ComponentWrapper>
    )
  }

  private renderItem(item: LogItem): JSX.Element {
    const timeLabel = DateHelper.getTimeLabel(item.timestamp)

    return (
      <LogWrapper key={item.timestamp}>
        <LogTimestamp>[{timeLabel}]</LogTimestamp>
        <LogMessage>{item.message}</LogMessage>
      </LogWrapper>
    )
  }
}

function mapStateToProps(state: StoreState) {
  const { trace } = state
  return {
    trace,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {}
}

const SectionLog = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseSectionLog)
export default SectionLog



