import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled, { ThemeProvider } from 'styled-components'
import * as Bows from 'bows'
import { StoreState } from 'src/store/interface'
import { ProgressState, GameState } from 'src/reducers'
import Header from './Header'
import SectionBattle from './SectionBattle'
import { GameAction, BattleAction } from 'src/actions'
import Tabs from './Tabs'
import { SectionHelper, GameHelper } from 'src/helpers'
import SectionLog from './SectionLog'

const COMPONENT_NAME = 'ViewGame'
const log = Bows(COMPONENT_NAME)

const theme = {}
let tickIntervalId: NodeJS.Timeout

const ComponentWrapper = styled.div`
  max-width: 800px;
  height: 100%;
  margin: 0 auto;
  // background-color: #e8ddcb;
  background-color: white;
`

const MainSection = styled.div`
  padding: 24px;
  background-color: rgba(119, 199, 199, 0.2);
`

const SectionContainer = styled.div`
  margin-top: 24px;
`

const SectionWrapper = styled.div``

interface Props {
  location: any
  progress: ProgressState
  game: GameState
  setActiveSection: (section: string) => Promise<void>
  appendRandomMob: () => Promise<void>
  performAutoBattle: () => Promise<void>
  performBattle: () => Promise<void>

  _placeholder: any
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseViewGame extends React.Component<Props, State> {
  static defaultProps = {
    _placeholder: '',
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')
    const tickSpeed = GameHelper.getTickIntervalMs(this.props.game)
    tickIntervalId = setInterval(() => this.tick(), tickSpeed)
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
    clearInterval(tickIntervalId)
  }

  render(): JSX.Element {
    return (
      <ThemeProvider theme={theme}>
        <ComponentWrapper>
          <Header />
          <SectionLog />
          <SectionBattle />
          <MainSection>
            <Tabs />
            <SectionContainer>{this.renderActiveSection()}</SectionContainer>
          </MainSection>
        </ComponentWrapper>
      </ThemeProvider>
    )
  }

  private renderActiveSection(): JSX.Element {
    const sectionItem = SectionHelper.getItemByKey(this.props.game.activeSection)
    if (!sectionItem) {
      return <div>Unknown Section</div>
    }

    return (
      <SectionWrapper>
        <sectionItem.Component />
      </SectionWrapper>
    )
  }

  private tick() {
    // log('tick triggered.')
    this.props.appendRandomMob()
    this.props.performAutoBattle()
    this.props.performBattle()
  }
}

function mapStateToProps(state: StoreState) {
  const { progress, game } = state
  return {
    progress,
    game,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    setActiveSection: async (section: string): Promise<void> => {
      dispatch(GameAction.setActiveSection(section))
    },

    appendRandomMob: async (): Promise<void> => {
      dispatch(GameAction.appendRandomMob())
    },

    performAutoBattle: async (): Promise<void> => {
      dispatch(BattleAction.performAutoBattle())
    },

    performBattle: async (): Promise<void> => {
      dispatch(BattleAction.performBattle())
    },
  }
}

const ViewGame = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseViewGame)
export default ViewGame



