import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import * as Bows from 'bows'
import { StoreState } from 'src/store/interface'
import { ProgressState, GameState, BattleState, PlayerState } from 'src/reducers'
import { MobHelper, DungeonHelper, PlayerHelper } from 'src/helpers'
// import Button from './Button'
import { CharacterItem } from 'src/common/interfaces'
import { BattleAction } from 'src/actions'
import { DungeonItem } from 'src/data'
import ListItem from './ListItem'

const COMPONENT_NAME = 'SectionDungeon'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  // padding: 24px;
`

const DescriptionContainer = styled.div`
  border: solid 1px aqua;
`

const MobContainer = styled.div`
  margin-top: 24px;
`

interface Props {
  progress: ProgressState
  game: GameState
  battle: BattleState
  player: PlayerState
  engageBattle: (mob: CharacterItem) => Promise<void>

  _placeholder: any
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseSectionDungeon extends React.Component<Props, State> {
  static defaultProps = {
    _placeholder: '',
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  get dungeonItem(): DungeonItem | undefined {
    return DungeonHelper.getItemByKey(this.props.game.currentLocation)
  }

  componentDidMount() {
    log('componentDidMount triggered.')
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  operatorClickHandler(mob: CharacterItem) {
    log('operatorClickHandler triggered. mob:', mob)
    this.props.engageBattle(mob)
  }

  render(): JSX.Element {
    return (
      <ComponentWrapper>
        {this.renderDescription()}
        {this.renderItems()}
      </ComponentWrapper>
    )
  }

  private renderDescription(): JSX.Element | null {
    if (!this.dungeonItem) {
      return null
    }

    return (
      <DescriptionContainer>
        <ul>
          <li>Dungeon: {this.dungeonItem.label}</li>
          <li>Queue Size: {PlayerHelper.getMobQueueSize(this.props.player.character!.currentLevel)}</li>
        </ul>
      </DescriptionContainer>
    )
  }

  private renderItems(): JSX.Element | null {
    if (!this.props.game.mobs || this.props.game.mobs.length === 0) {
      return null
    }

    return <MobContainer>{this.props.game.mobs.map((mob) => this.renderItem(mob))}</MobContainer>
  }

  private renderItem(mob: CharacterItem): JSX.Element | null {
    const mobTemplate = MobHelper.getMobTemplateByKey(mob.key)

    if (!mobTemplate) {
      log('Failed to find template for mob:', mob)
      return null
    }

    const heading = mobTemplate.name
    const subheading = `(Lvl ${mob.currentLevel})`
    // const blurb = 'Lorem ipsum dolor sit.'
    const blurb = ''

    let ctaLabel = 'Fight'
    if (this.props.battle.targetMob && this.props.battle.targetMob.id === mob.id) {
      ctaLabel = 'In Combat'
    }

    return <ListItem key={mob.id} heading={heading} subheading={subheading} blurb={blurb} ctaLabel={ctaLabel} onClick={() => this.operatorClickHandler(mob)} />
  }
}

function mapStateToProps(state: StoreState) {
  const { progress, game, battle, player } = state
  return {
    progress,
    game,
    battle,
    player,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {
    engageBattle: async (mob: CharacterItem): Promise<void> => {
      await dispatch(BattleAction.engageBattle(mob))
    },
  }
}

const SectionDungeon = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseSectionDungeon)
export default SectionDungeon



