import * as React from 'react'
import { connect } from 'react-redux'
import { Dispatch } from 'redux'
import styled from 'styled-components'
import * as Bows from 'bows'
import { StoreState } from 'src/store/interface'
import { ProgressState, GameState, BattleState, PlayerState } from 'src/reducers'
import { CharacterItem } from 'src/common/interfaces'
import { PlayerHelper, CharacterHelper } from 'src/helpers'

const COMPONENT_NAME = 'SectionBattle'
const log = Bows(COMPONENT_NAME)

const ComponentWrapper = styled.div`
  // background-color: rgba(255, 255, 0, 0.2);
`

const ComponentInnerWrapper = styled.div`
  padding: 24px;
  display: flex;
  // align-items: center;
  margin: 0 -12px;
`

const CharacterContainer = styled.div`
  flex-grow: 1;
  flex-basis: 45%;
  margin: 0 12px;
`

const InfoContainer = styled.div`
  border: solid 1px #036564;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 12px;
  flex-grow: 1;
  height: 100%;
`

const CaptionWrapper = styled.div`
  margin-bottom: 6px;
`

const Name = styled.div`
  font-weight: bold;
  display: inline-block;
`

const Level = styled.div`
  display: inline-block;
`

const StatWrapper = styled.div``

const StatKey = styled.div`
  display: inline-block;
`

const StatValue = styled.div`
  display: inline-block;
`

interface Props {
  progress: ProgressState
  game: GameState
  player: PlayerState
  battle: BattleState

  _placeholder: any
}

interface State {
  _placeholder: any
}

const defaultState: State = {
  _placeholder: undefined,
}

class BaseSectionBattle extends React.Component<Props, State> {
  static defaultProps = {
    _placeholder: '',
  }

  constructor(props: Props) {
    super(props)

    this.state = defaultState
  }

  componentDidMount() {
    log('componentDidMount triggered.')
  }

  componentWillUnmount() {
    log('componentWillUnmount triggered.')
  }

  render(): JSX.Element {
    return (
      <ComponentWrapper>
        <ComponentInnerWrapper>
          <CharacterContainer>{this.renderPlayerInfo()}</CharacterContainer>
          <CharacterContainer>{this.renderMobInfo()}</CharacterContainer>
        </ComponentInnerWrapper>
      </ComponentWrapper>
    )
  }

  private renderPlayerInfo(): JSX.Element {
    return this.renderCharacterInfo(this.props.player.character!)
  }

  private renderMobInfo(): JSX.Element | null {
    if (!this.props.battle.targetMob) {
      return null
    }
    return this.renderCharacterInfo(this.props.battle.targetMob)
  }

  private renderCharacterInfo(character: CharacterItem): JSX.Element {
    const name = CharacterHelper.getName(character)
    const displayExperience = character.key === 'player'

    return (
      <InfoContainer>
        <CaptionWrapper>
          <Name>{name}</Name> <Level>(Lvl {character.currentLevel})</Level>
        </CaptionWrapper>
        {displayExperience && (
          <StatWrapper>
            <StatKey>EXP:</StatKey>{' '}
            <StatValue>
              {character.currentExp} / {PlayerHelper.getExpRequiredToLevelUp(character.currentLevel)}
            </StatValue>
          </StatWrapper>
        )}
        <StatWrapper>
          <StatKey>HP:</StatKey>{' '}
          <StatValue>
            {character.currentHp} / {character.maxHp}
          </StatValue>
        </StatWrapper>
        <StatWrapper>
          <StatKey>Attack:</StatKey> <StatValue>{character.attack}</StatValue>
        </StatWrapper>
        <StatWrapper>
          <StatKey>Defense:</StatKey> <StatValue>{character.defense}</StatValue>
        </StatWrapper>
      </InfoContainer>
    )
  }
}

function mapStateToProps(state: StoreState) {
  const { progress, game, player, battle } = state
  return {
    progress,
    game,
    player,
    battle,
  }
}

function mapDispatchToProps(dispatch: Dispatch) {
  return {}
}

const SectionBattle = connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseSectionBattle)
export default SectionBattle



